import React, { useEffect } from 'react'
import { CustomCard } from '@tsamantanis/react-glassmorphism'
import '@tsamantanis/react-glassmorphism/dist/index.css'
import '../../assets/styles/Circle.scss'
import { Tabs, Radio, Space } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { layThongTinChiTietPhim } from '../../redux/actions/QuanLyRapAction';
import moment from 'moment';
import { StarOutlined } from '@ant-design/icons';
import { Rate } from 'antd';
import { map } from 'lodash';
import './Detail.css'
import { NavLink } from 'react-router-dom';

const { TabPane } = Tabs;
export default function Detail(props) {
    const filmDetail = useSelector(state => state.QuanLyPhimReducer.filmDetail);
    console.log({ filmDetail });
    const dispatch = useDispatch();
    useEffect(() => {
        //lấy thông tin param từ url
        let { id } = props.match.params;
        dispatch(layThongTinChiTietPhim(id));
    }, [])
    return (

        <div style={{ backgroundImage: `url(${filmDetail.hinhAnh})  , url(https://picsum.photos/id/1004/500/500)`, minHeight: '100vh', backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'cover' }}>
            <CustomCard style={{ minHeight: '100vh', padding: '200px' }}
                effectColor="#C780FF" // required
                color="#000000" // default color is white
                blur={100} // default blur value is 10px
                borderRadius={'0px'} // default border radius value is 10px

            >
                <div className="grid grid-cols-3 gap-4 text-white">
                    <div className="" style={{ backgroundImage: `url(${filmDetail.hinhAnh})  , url(https://picsum.photos/id/1004/500/500)`, minHeight: '350px', backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'cover' }}>

                    </div>
                    <div style={{ margin: 'auto' }}>
                        <p style={{ fontSize: '16px' }}>Ngày chiếu : {moment(filmDetail.ngayKhoiChieu).format('DD.MM.YYYY')}</p>
                        <p className="text-3xl -mb-0">{filmDetail.tenPhim} </p>
                        {/* <p className="text-xl">{filmDetail.moTa} </p> */}
                        <p style={{ fontSize: '16px' }} >Thời lượng chiếu phim : 120 phút</p>
                    </div>
                    <div className="..." style={{ margin: 'auto' }}>
                        <div className={`c100 p${filmDetail.danhGia * 10} `}>
                            <span>{filmDetail.danhGia * 10}% </span>
                            <div className="slice">
                                <div className="bar"></div>
                                <div className="fill"></div>
                            </div>
                        </div>
                        <div className="  text-2xl leading-3">
                            <div >
                                <Rate style={{ color: 'red' }} allowHalf defaultValue={filmDetail.danhGia / 2} />
                            </div>

                        </div>
                        <p className="text-base">Số người đánh giá {filmDetail.danhGia} </p>
                    </div>
                </div>
                <Tabs className="mt-5 " defaultActiveKey="1" centered>
                    <TabPane tab="Lịch chiếu " key="1" style={{ minHeight: '350px', color: '#fff'}}>
                        <div className="mt-10 container bg-white px-3 py-5"  >

                            <Tabs tabPosition={'left'}>
                                {filmDetail.heThongRapChieu?.map((htr, index) => {
                                    return <TabPane tab={<div className="flex mx-auto items-center">
                                        <img src={htr.logo} className="rounded-full" width="50" alt="..." />
                                        <div className="text-base ml-2">{htr.tenHeThongRap}</div>
                                    </div>} key={index}>

                                        {htr.cumRapChieu?.map((cumRap, index) => {
                                            return <div key={index} className="pb-3">
                                                <did className="flex">
                                                    <img src={cumRap.hinhAnh} style={{ width: '50px', height: '50px' }} />
                                                    <div className="pl-3">
                                                        <p className="mb-1 " style={{fontWeight:'700',fontSize:'15px'}}>{cumRap.tenCumRap}</p>
                                                        <p>{cumRap.diaChi}</p>
                                                    </div>
                                                </did>
                                                <div className="grid grid-cols-4">
                                                    {cumRap.lichChieuPhim?.slice(0, 12).map((lichChieu, index) => {
                                                        return <NavLink to={`/checkout/${lichChieu.maLichChieu}`} key={index} className="col-span-1 text-green-600 font-bold">
                                                            {moment(lichChieu.ngayChieuGioChieu).format('hh:mm A')}
                                                        </NavLink>
                                                    })}
                                                </div>
                                            </div>
                                        })}

                                    </TabPane>
                                })}

                            </Tabs>
                        </div>
                    </TabPane>
                    <TabPane className=" text-white mt-10" tab="Thông tin" key="2" style={{ minHeight: '350px' }} >
                        
                        <div className="grid grid-cols-12 gap-4 text-xl p-3">
                            <div className="grid col-span-3">
                                <div className="grid grid-cols-1 gap-4">
                                    <div className="...">Ngày công chiếu </div>
                                    <div className="...">Đánh giá </div>
                                    <div className="...">Tên phim</div>
                                    <div className="...">Thể loại</div>
                                    <div className="...">Định dạng </div>
                                    <div className="...">Quốc gia SX</div>
                                </div>

                            </div>
                            <div className="grid col-span-3">
                            <div className="grid grid-cols-1 gap-4">
                                    <div className="...">{moment(filmDetail.ngayKhoiChieu).format('DD.MM.YYYY')} </div>
                                    <div className="...">{filmDetail.danhGia} điểm </div>
                                    <div className="...">{filmDetail.tenPhim}</div>
                                    <div className="...">Hành động</div>
                                    <div className="...">...</div>
                                    <div className="...">...</div>
                                </div>
                            </div>
                            <div className="grid col-span-6">
                                <div>Nội dung</div>
                                <div>{filmDetail.moTa} </div>
                            </div>
                        </div>
                    </TabPane>
                    
                </Tabs>

            </CustomCard>

        </div>
    )
}
